import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "../src/assets/css/reset.css" //css
import "../src/assets/css/style.css" //css
import Axios from "axios";
import VueAxios from 'vue-axios'
import lazyPlugin from 'vue3-lazy'
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
createApp(App).use(store).use(router).use(VueAxios,Axios).use(lazyPlugin,{
    loading: require('./assets/img/loading2.gif'),//
    error: require('./assets/img/loading2.gif')  //
}).use(ElementPlus).mount("#app");
